@import "_schwartzCoding_variables";
@import "_elements";

* {
    box-sizing: border-box;
}

li {
    list-style: none;
}
body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
      display: grid;
      background-color: rgb(247, 236, 216);
  }

  main {
    line-height: 1.6;
    grid-column-start: 2;
    width: 76%;
    margin: 0 auto;
  }

  p {
    font-size: $fontMedium;
  }

  .hero {
    margin-top: 150px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 150px;
    .hero__photo {
        height: 70vh;
        width: 90%;
        background-image: url("https://source.unsplash.com/random/");
        background-size: cover;
        background-position: center center;
    }
    h2 {
        font-weight: 500;
        font-size: 32px;
        margin-top: 80px;
        margin-bottom: 90px;
    }
    p {
        font-size: $fontMedium;
        span {
            font-size: $fontMedium - 5px;
            margin-right: 50px;
            font-weight: 500;
        }
    }
}

.products {
    display: grid;
    grid-template-columns: 1fr;
    .product {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
        padding: 20px 0px;
        border-top: $border;
        grid-column-start: span 2;
        &:nth-child(2),
        &:nth-child(4) {
            div {
                order: 1;
            }
        }    
        p {
            margin-bottom: 50px;
        }
        .product__category {
            @extend %categoryTitle;
        }
    }
    .btns {
        display: flex;
        align-items: center;
    }
    .line {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-start: span 2;
        .product {
            grid-template-columns: 1fr;
            grid-column-start: span 1;
            p {
                font-size: 22px;
                margin-top: 30px;
            }
        }
    }
}

.links {
    margin-top: 100px;
    display: grid;
    border-top: $border;
    padding-top: 20px;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-bottom: 50px;
    .link {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        h4 {
            @extend %categoryTitle;
            margin-bottom: 30px;
        }
        span {
            font-size: 22px;
            margin-bottom: 20px;
        }
    }
}

a.btn {
    @extend %btn;
}

footer {
    grid-column-start: 2;
    background-color: #4d4c4c;
    width: 100%;
    padding: 100px 0;
    .footer__top {
        width: 76%;
        margin: 0 auto;
        color: white;
        display: grid;
        grid-template-columns: 1.5fr 1fr;
        span {
            font-size: 38px;
        }
        ul {
            width: 100%;
            display: flex;
            li {
                margin-right: 10px;
            }
        }
    }
}